.cartEmpty {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;


  h5 {
    font-size: 20px;
    font-weight: 600;
  }

  p {
    margin-bottom: 70px;
    color: grey;
    font-weight: 400;
    font-size: 16px;
  }
}


.btn {
  width: 100%;
  height: 55px;
  background-color: #9dd558;
  border-radius: 18px;
  border: 0;
  font-family: 'Inter', sans-serif;
  font-weight: 600;
  font-size: 16px;
  color: white;
  cursor: pointer;
  transition: 0.15s ease-in-out;

  &:hover {
    background-color: lighten(#9dd558, 5%);
  }

  &:active {
    background-color: darken(#9dd558, 10%);
  }
}