body {
  margin: 0;
  padding: 0;
  background-color: #e7f6ff;
  font-family: 'Inter', sans-serif;
}

button,
input {
  font-family: 'Inter', sans-serif;
}

.wrapper {
  background-color: #fff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;
}

.content {
  h1 {
    margin: 0;
  }
}

.search-block {
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  padding: 0 15px;

  input {
    border: 0;
    padding: 10px;
    font-size: 16px;
    width: 200px;
  }
}